<template>
  <div>
    <!---->
    <Breadcrumb class="breadcrumb">
      <BreadcrumbItem>
        <Icon type="ios-home-outline"></Icon>基础设置
      </BreadcrumbItem>
      <BreadcrumbItem class="curBreadcrumb">
        <Icon type="logo-buffer"></Icon> 资金设置
      </BreadcrumbItem>
      <span class="screenContent">
        <img
          style="width: 11px;margin-right: 8px;margin-left: 10px;"
          src="../assets/day.png"
        >
        {{ curTime }}</span>
      <span class="screenContent">
        <img
          style="width: 10.5px;margin-right: 8px"
          src="../assets/screen.png"
        >
        <a
          href="javascript:void(0)"
          @click="isShowScreen"
        >
          筛选
          <Icon type="ios-arrow-down"></Icon>
        </a>
      </span>
    </Breadcrumb>
    <Content
      v-if="isScreen"
      class="content screenStyle"
      :style="{padding: '24px', background: '#fff', marginBottom: '10px'}"
    >
      <Form
        :model="searchData"
        :label-width="62"
      >
        <Row>
          <Col
            :xs="{ span: 11 }"
            :lg="{ span: 6}"
          >
          <FormItem label="资金类别">
            <Input
              v-model="searchData.capitalName"
              placeholder="请填写资金类别"
            ></Input>
          </FormItem>
          </Col>
          <Col
            style="text-align: right"
            :xs="{ span: 1}"
            :lg="{ span: 2, offset: 1 }"
          >
          <Button
            type="primary"
            @click="getFundData()"
          >
            确认
          </Button>
          </Col>
        </Row>
      </Form>
    </Content>
    <Content
      class="content"
      :style="{padding: '14px 24px', minHeight: '220px', background: '#fff'}"
    >
      <Button
        type="primary"
        icon="md-add"
        @click="createData"
      >
        新增
      </Button>
      <Button
        type="primary"
        icon="ios-create"
        @click="modifyData"
      >
        修改
      </Button>
      <Button
        type="primary"
        icon="md-close"
        @click="delData"
      >
        删除
      </Button>
      <Button icon="md-print" @click.stop="printContent(3)">
        打印
      </Button>
      <Button icon="md-exit" @click="excelExcel('/capital/exportCapitalListExcel')">
        导出
      </Button>
      <Table
        ref="checkTable"
        highlight-row
        class="table"
        :columns="columns"
        :data="data"
        @on-current-change="selectRow"
      ></Table>
      <!-- 分页 -->
      <Page
        style="text-align: right"
        :total="total"
        :current="searchData.pageNo"
        :page-size="searchData.pageSize"
        show-sizer
        transfer
        @on-change="pageNoChange"
        @on-page-size-change="pageSizeChange"
      />
      <!--详情弹出框-->
      <Modal
        v-model="selTypeMod"
        class="selStyle specialWidth"
        :mask-closable="false"
      >
        <p slot="header">
          <span v-if="!isDisabled && isCreate">请设置资金类别信息</span>
          <span v-else>资金类别信息</span>
        </p>
        <Tabs
          :animated="false"
          class="innerTabStyle"
          value="name1"
        >
          <TabPane
            label="信息设置"
            name="name1"
          >
            <Form
              ref="form"
              :model="form"
              :rules="ruleValidateType"
              :label-width="80"
            >
              <Row>
                <Col span="24">
                <FormItem
                  label="资金类别"
                  prop="capitalName"
                >
                  <Input
                    v-model.trim="form.capitalName"
                    :disabled="isDisabled"
                    placeholder="请填写资金类别名称"
                  ></Input>
                </FormItem>
                </Col>
              </Row>
              <Col span="24">
              <FormItem label="备注" prop="capitalRemark">
                <Input
                  v-model.trim="form.capitalRemark"
                  :disabled="isDisabled"
                  type="textarea"
                  :autosize="{minRows: 1,maxRows: 5}"
                  placeholder="请填写备注"
                ></Input>
              </FormItem>
              </Col>
            </Form>
          </TabPane>
          <TabPane
            class="uploadStyle"
            label="附件"
            name="name2"
          >
            <uploadFile ref="uploadFile" :is-disabled="isDisabled" :form="form" :is-pdf="false" />
          </TabPane>
        </Tabs>
        <p
          slot="footer"
          style="text-align: left"
        >
          <Button icon="md-print">
            打印
          </Button>
          <Button icon="md-exit">
            导出
          </Button>
          <Button
            v-if="!isDisabled"
            style="float: right;margin-left: 8px"
            @click="resetType('form')"
          >
            取消
          </Button>
          <Button
            v-if="!isDisabled"
            style="float: right"
            type="primary"
            @click="submitType('form')"
          >
            确认
          </Button>
        </p>
      </Modal>
      <!--/详情弹出框-->
    </Content>
  </div>
</template>

<script>
import uploadFile from '@/components/fileUpload.vue'
import excelExcel from '@/mixins/exportExcel.js'
export default {
  name: 'FundManage',
  components: {
    uploadFile
  },
  mixins: [excelExcel],
  data() {
    // 自定义序号
    const indexMethod = (row) => {
      const index = row._index
      return this.searchData.pageSize * (this.searchData.pageNo - 1) + index + 1;
    }
    return {
      // 分页的总数
      total: 0,
      // 新建|编辑
      isCreate: false,
      isSelected: false,
      isDisabled: false,
      form: {
        capitalName: '',
        capitalRemark: ''
      },
      ruleValidateType: {
        capitalName: [
          { required: true, message: '请填写资金类别', trigger: 'blur' }
        ]
      },
      //
      currentRow: {},
      // 筛选内容
      searchData: {
        capitalName: '',
        ascending: false,
        orderBy: 'capitalId',
        pageNo: 1,
        pageSize: 10
      },
      isScreen: false,
      curTime: '',
      showContact: false,
      selTypeMod: false,
      checkText: '提交审核',
      columns: [
        {
          type: 'index',
          title: '序号',
          width: 60,
          align: 'center',
          indexMethod: indexMethod
        },
        {
          title: '资金类别',
          render: (h, params) => {
            return h('span', {
              style: {
                color: '#2d8cf0',
                cursor: 'pointer'
              },
              on: {
                click: () => {
                  this.showInfo(params.row)
                }
              }
            }, params.row.capitalName);
          }
        }
      ],
      data: [
      ]
    }
  },
  mounted() {
    this.getFundData()
    var myDate = new Date();
    const time = myDate.toLocaleString('chinese', { hour12: false });
    this.curTime = time.substring(0, 15)
  },
  methods: {
    pageNoChange(pageNo) {
      this.searchData.pageNo = pageNo
      this.getFundData()
    },
    pageSizeChange(pageSize) {
      this.searchData.pageSize = pageSize
      this.getFundData()
    },
    getFundData() {
      const that = this
      that.$set(that, 'data', []) // 需要做这个操作，以防空数据留至其他行
      that.$Spin.show({
        render: (h) => {
          return h('div', [
            h('Icon', {
              'class': 'demo-spin-icon-load',
              props: {
                type: 'ios-loading',
                size: 18
              }
            }),
            h('div', '加载中')
          ])
        }
      });
      that.commonUtil.ajaxPostUtil('/capital/searchCapital/list', that.searchData, (res) => {
        // 关闭数据加载
        that.$Spin.hide();
        if (res.code === 1) {
          const listData = res.data
          that.total = listData.totalCount
          that.data = listData.capitalList
        } else {
          that.$Message.error(res.msg);
        }
      }, (error) => {
        that.$Message.error('网络连接错误');
        // 关闭数据加载
        that.$Spin.hide();
      }, that)
      that.commonUtil.getAuditCountApi(that)
    },
    // 删除数据
    delData() {
      const that = this
      if (!that.isSelected) {
        that.$Message.warning('您未选择需要删除的内容');
        return
      }
      that.$Modal.confirm({
        title: '删除',
        content: '确认删除此数据吗？',
        onOk: () => {
          that.$Spin.show({
            render: (h) => {
              return h('div', [
                h('Icon', {
                  'class': 'demo-spin-icon-load',
                  props: {
                    type: 'ios-loading',
                    size: 18
                  }
                }),
                h('div', '加载中')
              ])
            }
          });
          that.commonUtil.ajaxDeleteUtil('/capital/deleteCapital/' + that.currentRow.capitalId, (res) => {
            // 关闭数据加载
            that.$Spin.hide();
            if (res.code === 1) {
              that.isSelected = false;
              that.$Message.success(res.msg);
              that.getFundData()
            } else {
              that.$Message.error(res.msg);
            }
          }, (error) => {
            that.$Message.error('网络连接错误');
            that.$Spin.hide();
          }, that)
        }
      });
    },
    // 打开详情弹出来
    showInfo(row) {
      const data = Object.assign({}, row)
      this.clearFormData()
      this.isDisabled = true;
      this.showData(data)
    },
    // 选中一行进行修改数据
    selectRow(currentRow, oldCurrentRow) {
      this.currentRow = currentRow
      this.isSelected = true;
    },
    // 修改数据
    modifyData() {
      // 显示弹出框名称
      this.isCreate = false;
      this.isDisabled = false;
      this.clearFormData()
      if (!this.isSelected) {
        this.$Message.warning('您未选择需要修改的内容');
        return
      }
      const row = Object.assign({}, this.currentRow);
      this.showData(row)
    },
    showData(data) {
      this.form = data
      this.$nextTick(() => {
        this.$refs.uploadFile.getFileItems()
      })
      this.selTypeMod = true
    },
    clearFormData() {
      this.form = {
        capitalName: '',
        capitalRemark: ''
      }
      this.$refs['form'].resetFields();
      const uploadFile = this.$refs.uploadFile
      if (uploadFile) {
        uploadFile.clearFileItems()
      }
    },
    // 新建
    createData() {
      this.isCreate = true;
      this.clearFormData();
      this.isDisabled = false;
      this.selTypeMod = true;
    },
    submitType(name) {
      const that = this;
      const form = that.form
      form.fileIds = that.$refs.uploadFile.getFileIds()
      that.$refs[name].validate((valid) => {
        if (valid) {
          that.$Spin.show({
            render: (h) => {
              return h('div', [
                h('Icon', {
                  'class': 'demo-spin-icon-load',
                  props: {
                    type: 'ios-loading',
                    size: 18
                  }
                }),
                h('div', '加载中')
              ])
            }
          });
          // 添加
          if (that.isCreate) {
            that.commonUtil.ajaxPostUtil('/capital/addCapital', form, (res) => {
              // 关闭数据加载
              that.$Spin.hide();
              if (res.code === 1) {
                that.selTypeMod = false;
                that.$Message.success(res.msg);
                that.getFundData()
              } else {
                that.$Message.error(res.msg);
              }
            }, (error) => {
              that.$Message.error('网络连接错误');
              // 关闭数据加载
              that.$Spin.hide();
            }, that)
          } else {
            that.commonUtil.ajaxPutUtil('/capital/updateCapital', form, (res) => {
              // 关闭数据加载
              that.$Spin.hide();
              if (res.code === 1) {
                that.selTypeMod = false;
                that.$Message.success(res.msg);
                that.getFundData()
              } else {
                that.$Message.error(res.msg);
              }
            }, (error) => {
              that.$Message.error('网络连接错误');
              // 关闭数据加载
              that.$Spin.hide();
            }, that)
          }
          that.currentRow = {}
        } else {
          that.$Message.error('请填写好信息');
        }
      })
    },
    resetType(name) {
      this.$refs[name].resetFields();
      this.selTypeMod = false;
    },
    // 筛选按钮
    isShowScreen() {
      this.isScreen = !this.isScreen;
    }
  }
}
</script>

<style>
.ivu-drawer-body > div {
  margin: 20px 0;
}
.ivu-drawer-body > div b {
  margin-left: 3px;
}
/*<!--选择弹出框的样式-->*/
.selStyle .ivu-radio-wrapper {
  font-weight: bold;
}
.selStyle .ivu-modal-body {
  line-height: 2rem;
}
.selStyle .ivu-radio-wrapper {
  margin-right: 21px;
}
.ivu-page-options-sizer {
  margin-right: 0;
}
.table {
  margin: 15px 0;
}
.breadcrumb i {
  margin-right: 8px;
}
.ivu-breadcrumb {
  font-size: 12px;
  margin: 20px 0;
}
.ivu-menu-submenu-title {
  font-weight: bold;
}
.ivu-menu-submenu-title span > i,
.ivu-menu-submenu-title > i {
  margin-right: 4px;
}
.ivu-menu-vertical .ivu-menu-item,
.ivu-menu-vertical .ivu-menu-submenu-title {
  font-size: 12px;
  color: #495060;
}
.ivu-menu-light.ivu-menu-vertical .ivu-menu-item-active:not(.ivu-menu-submenu) {
  font-weight: bold;
}
.ivu-menu-submenu.ivu-menu-opened {
  padding-top: 8px;
}
</style>




